.appMain {
  height: 100vh;
  background-color: teal;
}

.main {
  position: relative;
  height: calc(100vh - 40px);
  overflow-x: hidden;
}

.notificationsWrapper {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 5px;
  z-index: 5;
}
