.sideMenu {
  height: 100%;
  min-width: 200px;
  background-color: white;
  transition: transform 250ms linear;

  &.isOpened {
    transform: translateX(200px);
  }

  ul {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;
    list-style: none;
  }

}
